  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 30px;
  height: 800px;
  position: relative;
  z-index: 1;
}

.heroBg {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-image: url(/static/media/banner-image2.220cc3ac.png);
  background-repeat: repeat;
  background-position: fixed;
}

ImgBg {
  width: 100%;
