}

.dropdown-container {
  position: relative;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0 1 rem;
  height: 100%;
  cursor: pointer;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  bottom: -90px;
}

.dropdown-content .dropdown-link {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown-content:hover .dropdown-link {
  background-color: #04aa6d;
  color: #fff;
  transition: all 0.2s ease-in-out;
}

.dropdown-content:hover .dropdown-link:hover {
  background-color: #058052;
  color: #fff;
  transition: all 0.2s ease-in-out;
}

.dropdown-container:hover .dropdown-content {
  display: block;
}

.dropdown-container:hover .profileIcon {
  color: #01bf71;
}

@media screen and (max-width: 960px) {
